  /**
* @author ZGY
* @date 2018/8/8 0008
* @Description:
*disabled
* <mt-button-once size="large" type="danger"  @click="$router.push({path: '/device'})"></mt-button-once>
*autofalsedisabledtrue
*<mt-button-once size="large" type="primary" class="btn-margin" @click.native="regist" :auto="false" :disabled="operatable"></mt-button-once>
*dataoperatable: true,
*mt-button
*/

<template>
  <mt-button v-if="auto" :plain="plain" :type="type" :size="size" :icon="icon" :disabled="!disabled" @click.native="autoClick">
    <slot></slot>
  </mt-button>
  <mt-button v-else :plain="plain" :type="type" :size="size" :icon="icon" :disabled="!disabled" @click.native="click">
    <slot></slot>
  </mt-button>
</template>

<script>
    export default {
        name: "mt-button-once",
        // 
        extends: {},
        // 
        props: {
          disabled:{default :true},
          plain:{default :false},
          type:{default :'default'},
          size:{default :'normal'},
          icon:{default :''},
          auto:{default :true},
        },
        // 
        data() {
            return {}
        },
        //
        computed: {},
        // 
        components: {},
        // 
        watch: {},
        // 
        methods: {
          autoClick(){
            this.disabled=false
            console.log('')
            this.$emit('click')
          },
          click(){
            console.log('')
            this.$emit('click')
          },
        },
        // 
        created() {
        },
        mounted() {
        },
    }
</script>
<style scoped>

</style>




