import Vue from 'vue'
import moment from 'moment'
import { moneyHandler } from './util'

const oFilters = {
  // thousands()thousands('')
  /*
   * 
   * @param value
   * @param formatString
   * @returns {*}
   */
  moment (value, formatString) {
    if (value) {
      value = Number(value)
      formatString = formatString || 'YYYY-MM-DD HH:mm:ss'
      return moment(value).format(formatString)
    } else {
      return ''
    }
  },
  /*
   *  -
   * @param value
   * @param formatString
   * @returns {*}
   */
  formatDuring(mss) {
    let days = parseInt(mss / (1000 * 60 * 60 * 24));
    let hours = parseInt((mss % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
    let minutes = parseInt((mss % (1000 * 60 * 60)) / (1000 * 60));
    let seconds = (mss % (1000 * 60)) / 1000;
    seconds=parseInt(seconds)
    if(days>0)
      return days + "  " + hours + "  " + minutes + "  " + seconds + "  ";
    else if(hours>0)
      return hours + "  " + minutes + "  " + seconds + "  ";
      else if(minutes>0)
        return minutes + "  " + seconds + "  ";
        else if(seconds>0)
          return seconds + "  ";
          else
            return mss + "  ";
  },
    /**
   * 
   * @param value
   * @param isPenny  valuetrue
     * @param unit 0 
   * @returns {string}
   */
  money (value, isPenny, unit = true) {
    return moneyHandler(value, isPenny, unit)
  },
  /**
   * 
   * @param value
   * @returns {string}
   */
  thousands(value){
    value += ''
    let pattern = /(?=((?!\b)\d{3})+$)/g
    return value.replace(pattern, ',')
  },

  /**
   * 
   * @param value
   * @returns {string}
   */
  phoneHide (value) {
    value += ''
    let iLen = value.length
    return iLen == 11 ? value.substring(0, 3) + "****" + value.substring(iLen - 4, iLen) : value
  },
  /**
   * 
   * @param value
   * @returns {string}
   */
  card4num (value, len) {
    len = len || 4
    let cardNum = value + '',
      cardLen = cardNum.length
    cardNum = cardNum.split('').splice(cardLen - len, len)
    return cardNum.join('')
  },

}

function Filters() {
  Object.keys(oFilters).forEach(function(key, index){
    Vue.filter(key, oFilters[key])
  })
}

//
export default new Filters()



