/*
 * @class   state
 * @author  
 * @date    17.05.22
 */

import userAPI from '../../api/userAPI'

// initial state
let state = {
  user: {},
  auth: {},
  weiXin: {},
  logined: false
}

// getters
const getters = {
  user: state => state,
  auth: state => state,
  logined: state => state
}

// actions
const actions = {
  login({commit}, params) {
    params = params || {}
    return new Promise((resolve, reject) => {
      userAPI.login(params, function (res) {
        userAPI.signin({
          requestToken: res.data.requestToken
        }, function (res) {
          //
          commit('CLEAR_USER')
          //token
          commit('SET_TOKEN', res.data)
          resolve(res)
        })
      })
    })
  },

  signin({commit}) {
    return new Promise((resolve, reject) => {
      let token=sessionStorage.getItem("requestToken")
      userAPI.signin({
        requestToken: token
      }, function (res) {
        commit('SET_TOKEN', res.data)
        resolve(res)
      })
    })
  },

  reg({commit}, params) {
    params = params || {}
    return new Promise((resolve, reject) => {
      userAPI.reg(params, function (res) {
        userAPI.signin({
          requestToken: res.data.requestToken
        }, function (res) {
          //
          commit('CLEAR_USER')
          commit('SET_TOKEN', res.data)
          resolve(res)
        })
      })
    })
  },

  logout({commit}) {
    return new Promise((resolve, reject) => {
      userAPI.logout(function (res) {
        commit('CLEAR_USER')
        commit('REMOVE_TOKEN')
        resolve(res)
      })
    })
  },

  /**
   * 
   * @param commit
   * @return {Promise<any>}
   */
  getUserDetails({commit}) {
    return new Promise((resolve, reject) => {
      userAPI.getDetails({}, function (res) {
        /** todo 
        {
          id: "1", //Id
          displayName: "", // 
          employeeNo: "10000", // 
          weixinAccountNo: "xxxxx", // 
          weixinQRCode: "http://xxxx.jpg", // 
          level: "1", // (1=2=3=4=5=6=7=)
          identityId: "1", // Id
          identity: {
            realName: "", // 
            IDCardNo: "44098xxx", // 
            mobile: "136xxxx", //
            IDCardFrontPicture: "http://xxx.jpg", // 
            IDCardHandPicture: "http://xxx.jpg" // 
          }
        }*/
        res.data.level = 2  // 
        sessionStorage.setItem("user", JSON.stringify(res.data||[]))
        commit('SET_USER_DETAILS', res.data)
        resolve(res)
      })
    })
  },

  /**
   * 
   * @param commit
   * @return {Promise<any>}
   */
  setUserDetail({commit}, data) {
    commit('SET_USER_DETAILS', data)
  },

  saveAvatar({commit}, params) {
    params = params || {}
    return new Promise((resolve, reject) => {
      userAPI.saveAvatar(params, function (res) {
        resolve(res)
      })
    })
  },
}

// mutations
const mutations = {
  SET_USER_DETAILS(state, data) {
    state.user = Object.assign({}, state.user, data) // compute
    let _data = data.weixin
    if (data.platform) {
      _data = data.platform
    }
    if(_data && _data.shortName) {
      window.COMPANY_NAME = _data.shortName
      window.WEIXIN_NAME = _data.accountName
      window.APP_TYPE = _data.appType // 100=,200=
    }
  },

  CLEAR_USER(state) {
    state.user = {}
  },

  SET_TOKEN(state, data) {
    state.auth = Object.assign({
      expiredTime: new Date().getTime() + data['expiresIn'] * 1000
    }, state.auth, data) // compute

    sessionStorage.setItem("session", JSON.stringify(state.auth))

    state.logined = true
  },

  REMOVE_TOKEN(state) {
    state.auth = {}

    sessionStorage.removeItem('session')

    state.logined = false
  },
}

export default {
  state,
  getters,
  actions,
  mutations
}



