/**
* @author ZGY
* @date 2018/7/23 0023
* @Description: field 
*  valuev-model  attr
<!--*
<mt-field-arrow selects="address" title=""  placeholder=""  @imgClick="showMap"  :value="paramsData.address" @input="value => { paramsData.address = value }" ></mt-fiel>
<mt-field-arrow title=""  placeholder="" @click.native='showSearchUserPopup'  :value="signedUser" @input="value => { signedUser = value }" ></mt-field-arrow>
-->
*/

<template>
  <mt-cell v-if="selects=='cell'" class="form-label" :title="title"  @input="handleInput"  is-link>
    <span  :style="value==''?('color: #888'):('color: black')">{{value?value:placeholder}}</span>
  </mt-cell>
  <a v-else class="mint-cell mint-field" :class=selects>
    <div class="mint-cell-left"></div>
    <div class="mint-cell-wrapper">
      <div class="mint-cell-title">
        <span class="mint-cell-text">{{title}}</span>
      </div>
      <div v-if="selects=='dropDownList'" class="mint-cell-value" @click.native="click">
        <input :placeholder=placeholder :value="value" @input="handleInput"   type="text" class="mint-field-core"  maxlength="50" :attr=attr>
        <div class="mask-layer" ></div>
      </div>
      <div v-else class="mint-cell-value">
        <input :placeholder=placeholder :value="value" @input="handleInput"   type="text" class="mint-field-core" :attr=attr>
          <div class="mint-field-clear" style="display: none;">
            <i class="mintui mintui-field-error"></i>
          </div>
          <div class="mint-field-other">
            <img  @click="clickImg" class="address-icon" src="~@/assets/img/location.png" width="20" height="20">
          </div>
      </div>
    </div>
    <i v-if="selects=='dropDownList'" class="mint-cell-allow-right"></i>
  </a>
</template>

<script>
    export default {
        name: "mt-field-arrow",
        // 
        extends: {},
        // 
        props: {
          selects:{default :'dropDownList'},//dropDownListaddress 
          value:{required :true},
          title:{default :''},
          placeholder:{default :''},
          attr:{}
        },
        // 
        data() {
            return {}
        },
        //
        computed: {},
        // 
        components: {},
        // 
        watch: {},
        // 
        methods: {
          handleInput(event) {
            // 2input
            let value = event.target.value
            this.$emit('input', val);
          },
          click(){
            console.log('click')
             this.$emit('click')
          },
          clickImg(){
            console.log('clickImg')
            this.$emit('imgClick')
          },
        },
        // 
        beforeCreate() {
        },
        created() {
          console.log('mt-field-arrow')
        },
        mounted() {
        },
    }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  .dropDownList{
  .mint-cell-title{
    flex: none;
    width: 105px;
  }

  .mint-cell-value{
    position: relative;
  }

  .mask-layer{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
  }
  }
  .mask-layer{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
  }
  .address{

    input[class="mint-field-core"]{
      flex: none;
      width: 90%;
    }
    .mint-field-other{
      position: absolute;
      top: 16px;
      right: 14px;
    }
  }
</style>




