/*
 * @class  
 * @Author 
 */
import metadataAPI from '../../api/metadataAPI'

// initial state
let state = {
  metadata: {
    payAmount: 200
  },
  initialized: false
}

// getters
const getters = {
  metadata: state => state,
  initialized: state => state
}

// actions
const actions = {
  getDataSet({commit}, params) {
    return new Promise((resolve, reject) => {
      metadataAPI.getDataSet({}, function (res) {
        // 
        commit('SET_DATASET', res.data)
        resolve(res)
      })
    })
  }
}

// mutations
const mutations = {
  SET_DATASET(state, data) {
    /** todo :
    {
      promotionExperience: [], // 
      productTag: [], // 
      bank: [] // 
    }
    */
    sessionStorage.metadata = JSON.stringify(data)
    state.metadata = Object.assign({}, state.metadata, data) // compute
    state.initialized = true
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



